.projects-section {
    padding: 50px 0;
}

.project-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
}

.project {
    background-color: #2eaeea;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(45, 5, 244, 0.1);
}

.project h3 {
    margin-bottom: 10px;
}

.project p {
    margin-bottom: 15px;
}

/* Max styling */
.project {
    transition: transform 0.3s ease;
}

.project:hover {
    transform: translateY(15px);
}