/* Header */
.header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #003d69;
   
    color: #FFFFFF;
   
}

.profile {
    margin-right: 20px;
}

.profile-pic {
    width: 288px;
    height: 288px;
    border-radius: 50%;
    transition: width 0.3s ease, height 0.3s ease, transform 1s ease;
    /* Add transform transition */
}

.profile-pic:hover {
    transform: rotate(360deg);
    /* Rotate the profile pic 360 degrees when hovering */
}

@keyframes pulse {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.2);
    }
}

.name {
    font-size: 34px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
    animation: pulse 2s infinite alternate;
    transition: yellow 0.3s ease;
    
}

.navigation {
    ul {
        list-style-type: none;
        padding: 0;
        display: flex;
    }

    li {
        margin-right: 20px;
    }

    a {
        color: #FFD700;
       
        text-decoration: none;
        transition: color 0.3s ease;
        
    }

    a:hover {
        color: #00FFFF;
        
    }

    .resume-btn {
        background-color: #FF6347;
        color: #FFFFFF;
        border: none;
        padding: 10px;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease;
        animation: pulse 2s infinite alternate;
      
        
    }

    .resume-btn:hover {
        background-color: #FF4500;
        
       
        
    }
}