.about-section {
    padding: 100px 0;
    background-color: #f9f9f9;
    transform: translateY(15px);
}

h2{
    text-align: center;
}
.about-content {
    max-width: 800px;
    margin: 0 auto;
    line-height: 1.6;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    
}

.about-content p {
    margin-bottom: 20px;
    background-color: #fefefe;
}

.linkedin-link {
    color: #0077B5;
    text-decoration: none;
    font-weight: bold;
    transition: color 0.3s ease;
}

.linkedin-link:hover {
    color: #00405E;
}

.about-content {
    animation: fadeInUp 1s ease;
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }

    100% {
        opacity: 2;
        transform: translateY(0);
    }
}