.skills-section {
    padding: 50px 0;
    background-color: #0077be;
    /* Ocean blue background */
}

.skills-container {
    display: flex;
    justify-content: space-between;
    max-width: 800px;
    margin: 0 auto;
}

.technical-skills,
.soft-skills {
    flex: 1;
    margin-right: 20px;
    margin-left: 20px;
    background-color: #46d2f5;
    /* White background */
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 4px 8px rgba(3, 2, 2, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.technical-skills:hover,
.soft-skills:hover {
    transform: translateY(-10px);
    /* Move up on hover */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    /* Increase shadow on hover */
}

.skills-container h3 {
    margin-bottom: 20px;
    color: #fff;
    /* White text */
}

.skills-container ul {
    list-style-type: none;
    padding: 0;
}

.skills-container ul li {
    margin-bottom: 10px;
    color: #090909;
    /* Light text color */
}

