.contact-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #0077be;
    /* Ocean blue background */
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.contact-container h2 {
    font-family: 'Arial', sans-serif;
    color: #fff;
    /* White text */
}

.contact-form {
    margin-top: 20px;
}

.form-group {
    margin-bottom: 15px;
    text-align: left;
}

label {
    font-weight: bold;
    color: #fff;
    /* White text */
}

input[type="text"],
input[type="email"],
textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 16px;
    background-color: #e0f0ff;
  
    
}

.submit-btn,
#emailBtn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.submit-btn {
    background-color: #0056b3;
   
    color: #fff;
   
    margin-bottom: 15px;
}

.submit-btn:hover {
    background-color: #004080;
    
}

#emailBtn {
    background-color: #53d0ec;
  
    color: #fff;
    
}

#emailBtn:hover {
    background-color: #153f7a;
    
}
.social-icons {
    display: flex;
    justify-content: center;
    /* Align items to the center horizontally */
    margin-top: 20px;
    /* Add some top margin for spacing */
}

.social-icons ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    align-items: center;
    /* Align items vertically to the center */
}

.social-icons li {
    margin-right: 20px;
}

.social-icons a {
    color: #ffd700;
    text-decoration: none;
    transition: color 0.3s ease;
}

.social-icons a:hover {
    color: #00ffff;
}